import { buildSVG, getNounData, getNounSeedFromBlockHash, getRandomSeed } from './protocol/index.js';
import { getNumberFromString, getSeedArrayFromSeed, getSeedFromSeedArray, getSeedFromTraitNames, getTraitNamesFromSeed } from './utils/index.js';
export class Factory {
    config;
    dao;
    meta;
    palette;
    bgcolors;
    layers;
    constructor(config) {
        this.config = config;
        const { dao, meta, layer_data } = this.config;
        const { palette, bgcolors, layers } = layer_data;
        this.dao = dao;
        this.meta = meta;
        this.palette = palette;
        this.bgcolors = bgcolors;
        this.layers = layers;
    }
    blanks = {
        background: 'transparent',
        dataLayer: { filename: 'blank', data: '0x0000000000' },
        seed: { background: 9000, body: 9000, accessory: 9000, head: 9000, glasses: 9000 },
        seedArray: [9000, 9000, 9000, 9000, 9000],
        traits: { background: '', body: '', accessory: '', head: '', glasses: '' }
    };
    /**
     * Create a noun with specific traits. Missing traits will be randomly chosen.
     * @param {Object} [opts]
     * @param {string} [opts.background]
     * @param {string} [opts.body]
     * @param {string} [opts.accessory]
     * @param {string} [opts.head]
     * @param {string} [opts.glasses]
     * @param {number} [opts.size] Sets the width & height of the image
     */
    create(opts) {
        const seed = this.getSeedFromTraitNames(opts);
        return this.buildNoun(seed, opts?.size);
    }
    /**
     * Create the latest noun from the Nouns or Lil Nouns DAO
     * @param {Object} [opts]
     * @param {string} [opts.bgColor] A valid color string to use as the background color
     * @param {number} [opts.size] Sets the width & height of the image
     */
    async createFromLatest(opts) {
        const response = await fetch(this.meta.urls.api + '/latest/seed');
        let seed = await response.json();
        if (opts?.bgColor)
            seed = this.updateBgProperties(seed, opts.bgColor);
        return this.buildNoun(seed, opts?.size);
    }
    /**
     * Create an official noun from the Nouns or Lil Nouns DAO
     * @param id The ID of the Noun
     * @param {Object} [opts]
     * @param {string} [opts.bgColor] A valid color string to use as the background color
     * @param {number} [opts.size] Sets the width & height of the image
     */
    async createFromId(id, opts) {
        const response = await fetch(`${this.meta.urls.api}/${id}/seed`);
        let seed = await response.json();
        if (opts?.bgColor)
            seed = this.updateBgProperties(seed, opts.bgColor);
        return this.buildNoun(seed, opts?.size);
    }
    /**
     * Create a noun from a predefined seed object
     * @param {Object} seed
     * @param {number} seed.background
     * @param {number} seed.body
     * @param {number} seed.accessory
     * @param {number} seed.head
     * @param {number} seed.glasses
     * @param {Object} [opts]
     * @param {string} [opts.bgColor] A valid color string to use as the background color
     * @param {number} [opts.size] Sets the width & height of the image
     */
    createFromSeed(seed, opts) {
        if (opts?.bgColor)
            seed = this.updateBgProperties(seed, opts.bgColor);
        return this.buildNoun(seed, opts?.size);
    }
    /**
     * Generate a noun from a given string
     * @param string The text used to generate the noun
     * @param {Object} [opts]
     * @param {string} [opts.bgColor] A valid color string to use as the background color
     * @param {number} [opts.size] Sets the width & height of the image
     */
    createFromString(string, opts) {
        const id = getNumberFromString(string);
        let seed = getNounSeedFromBlockHash(this.config, id, opts?.hash);
        if (opts?.bgColor)
            seed = this.updateBgProperties(seed, opts.bgColor);
        return this.buildNoun(seed, opts?.size);
    }
    /**
     * Create a random noun
     * @param {Object} [opts]
     * @param {string} [opts.bgColor] A valid color string to use as the background color
     * @param {number} [opts.size] Sets the width & height of the image
     */
    createRandom(opts) {
        let seed = this.getRandomSeed();
        if (opts?.bgColor)
            seed = this.updateBgProperties(seed, opts.bgColor);
        return this.buildNoun(seed, opts?.size);
    }
    buildNoun(seed, size) {
        const { meta } = this.config;
        const { urls } = meta;
        const { palette, parts, background } = getNounData(this.config, seed, this.blanks);
        const svg = buildSVG(parts, palette, background, size);
        const traits = this.getTraitNamesFromSeed(seed);
        const seedArray = this.getSeedArrayFromSeed(seed);
        const noun = {
            background: traits.background,
            body: traits.body,
            accessory: traits.accessory,
            head: traits.head,
            glasses: traits.glasses,
            seed: seed,
            svg: 'data:image/svg+xml;base64,' + btoa(svg),
            url: urls.api + '/pfp?seed=' + seedArray.join(',')
        };
        if (size && size !== 320)
            noun.url += '&size=' + size;
        return noun;
    }
    updateBgProperties(seed, bgColor) {
        if (bgColor === 'transparent') {
            seed.background = 9000;
        }
        else
            seed.background = 2;
        seed.bgColor = bgColor;
        return seed;
    }
    // util methods
    getRandomSeed() {
        return getRandomSeed(this.config);
    }
    getSeedArrayFromSeed(seed) {
        return getSeedArrayFromSeed(seed);
    }
    getSeedFromSeedArray(seedArray) {
        return getSeedFromSeedArray(seedArray);
    }
    getSeedFromTraitNames(opts) {
        return getSeedFromTraitNames(this.config, this.getRandomSeed(), this.blanks, opts);
    }
    getTraitNamesFromSeed(seed) {
        return getTraitNamesFromSeed(this.config, seed, this.blanks);
    }
}
