import Hashes from 'jshashes';
export const getNumberFromString = (str) => {
    str = str.trim();
    const hashes = [
        str,
        new Hashes.MD5().hex(str),
        new Hashes.SHA1().b64(str),
        new Hashes.SHA256().hex(str),
        new Hashes.SHA512().b64(str),
        new Hashes.RMD160().b64(str)
    ];
    const chars = hashes.reduce((a, b) => a + b, '');
    const charArray = Array.from(chars);
    const sum = charArray.reduce((a, b) => a + b.charCodeAt(0), 0);
    return sum * str.length;
};
export const getSeedFromTraitNames = (config, seed, blanks, traits) => {
    const { layer_data } = config;
    const { bgcolors, layers } = layer_data;
    Object.entries(traits || {}).forEach(([trait, value]) => {
        if (trait === 'background') {
            if (value === 'transparent')
                seed.background = 9000;
            else if (bgcolors.includes(value)) {
                seed.background = bgcolors.findIndex((color) => color === value);
            }
            else {
                seed.background = 2;
                seed = { ...seed, bgColor: value };
            }
        }
        else if (Object.keys(seed).includes(trait)) {
            const layer = trait;
            if (value === 'none')
                seed[layer] = 9000;
            else {
                const index = layers[layer].findIndex((t) => value === t.filename);
                seed[layer] = index;
            }
        }
    });
    return seed;
};
export const getTraitNamesFromSeed = (config, seed, blanks) => {
    const { layer_data } = config;
    const { bgcolors, layers } = layer_data;
    const traits = blanks.traits;
    Object.entries(seed).forEach(([trait, value]) => {
        if (trait === 'background') {
            if (value === 9000)
                traits.background = 'transparent';
            else if (value === 2 && seed.bgColor)
                traits.background = seed.bgColor;
            else
                traits.background = '#' + bgcolors[value];
        }
        else if (Object.keys(traits).includes(trait)) {
            const layer = trait;
            if (value === 9000)
                traits[layer] = 'none';
            else {
                traits[layer] = layers[layer][value].filename;
            }
        }
    });
    return traits;
};
export const getSeedArrayFromSeed = (seed) => {
    const seedArray = [
        seed.background,
        seed.body,
        seed.accessory,
        seed.head,
        seed.glasses
    ];
    if (seed.bgColor)
        seedArray.push(seed.bgColor?.replace(/#/g, ''));
    return seedArray;
};
export const getSeedFromSeedArray = (arr) => {
    const seed = {
        background: arr[0],
        body: arr[1],
        accessory: arr[2],
        head: arr[3],
        glasses: arr[4]
    };
    if (arr[5])
        seed.bgColor = '#' + arr[5];
    return seed;
};
